html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #eaefbd;
  color: #FFFFFF;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Anton", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3, h4 {
  font-family: "Anton", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #37371f;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

h4 {
  font-size: 2.6rem;
  text-align: center;
}

p {
  font-family: "PT Sans", sans-serif;
  color: #FFFFFF;
  font-size: 1.8rem;
}

.main-heading {
  margin: 3rem 0;
}

.hidden {
  display: none;
}

.btn {
  border: none;
  cursor: pointer;
  padding: 2rem;
  text-align: center;
  color: #FFFFFF;
  border-radius: 1rem;
  text-transform: uppercase;
  font-family: "Anton", sans-serif;
  font-weight: 400;
  font-size: 2rem;
}

.btn-orange {
  background-color: #ea9010;
  transition: background-color 300ms ease;
}
.btn-orange:hover {
  background-color: #ba730d;
}

.btn-ok {
  background-color: rgb(255, 162, 0);
  transition: background-color 300ms ease;
}
.btn-ok:hover {
  background-color: #cc8200;
}

.btn-green {
  background-color: #90be6d;
  transition: background-color 300ms ease;
}
.btn-green:hover {
  background-color: #76ac4c;
}

.btn-red {
  background-color: #cb0000;
  transition: background-color 300ms ease;
}
.btn-red:hover {
  background-color: #980000;
}

.btn-blue {
  background-color: #0da6f3;
  transition: background-color 300ms ease;
}
.btn-blue:hover {
  background-color: #0a85c3;
}

.btn-gray {
  padding: 1rem;
  border-radius: 0;
  background-color: #4b4b4b;
  transition: background-color 300ms ease;
}
.btn-gray:hover {
  background-color: #323232;
}

.dsp-block {
  display: block;
}

header {
  background-color: #FFFFFF;
  padding: 1.3rem 0;
}
@media (min-width: 768px) {
  header .header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
header .header-content .header-image {
  text-align: center;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  header .header-content .header-image {
    margin: 0;
  }
}
header .header-content .header-image img {
  max-width: 25rem;
}

.header-nav a {
  border-bottom: #c8c8c8 solid 0.1rem;
}
@media (min-width: 768px) {
  .header-nav a {
    border: none;
  }
}
.header-nav .hamburguer-button {
  color: #FFFFFF;
  background-color: #90be6d;
  padding: 1rem;
  border-radius: 1rem;
  margin: 0;
  transition: background-color 300ms ease;
  cursor: pointer;
}
.header-nav .hamburguer-button:hover {
  background-color: #76ac4c;
}
@media (min-width: 768px) {
  .header-nav .hamburguer-button {
    display: none;
  }
}
@media (min-width: 768px) {
  .header-nav .nav {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
  }
}

.nav-both .nav a {
  font-size: 2;
  font-weight: 400;
  color: #76ac4c;
  display: block;
  text-align: center;
  margin-bottom: 1rem;
  transition: color 300ms ease;
}
.nav-both .nav a:last-child {
  margin: 0;
}
.nav-both .nav a:first-child {
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .nav-both .nav a {
    display: unset;
    margin: 0;
  }
  .nav-both .nav a:first-child {
    margin-top: 0;
  }
}
.nav-both .nav a:hover {
  color: #ea9010;
}

.footer {
  background-color: #FFFFFF;
}
.footer .contenedor-footer {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .footer .contenedor-footer {
    padding: 5rem 0;
  }
}

.copyright {
  background-color: #37371f;
}

.footer-copyright {
  text-align: center;
  color: #FFFFFF;
  padding: 1rem 0;
}
.footer-copyright p {
  margin: 0;
}

.home {
  background-image: url(../img/index-background.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  position: relative;
  height: 50rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
.home::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 0;
}

@media (min-width: 1024px) {
  .home-text {
    width: 60%;
  }
}
.home-text h1,
.home-text p {
  position: relative;
  z-index: 1;
}
.home-text h1 {
  color: #FFFFFF;
}
.home-text p {
  color: #eaefbd;
  font-size: 2.2rem;
  text-align: center;
  font-weight: 400;
  padding-bottom: 4rem;
}
.home-text a {
  position: relative;
  z-index: 1;
}

.index-explore {
  padding: 2.2rem 0;
  margin-bottom: 3rem;
  text-align: center;
}
.index-explore:last-of-type {
  margin-bottom: 6rem;
}

.seccion-iconos {
  background-color: #90be6d;
}
.seccion-iconos .contenedor-iconos {
  padding: 2rem 0;
}
@media (min-width: 1024px) {
  .seccion-iconos .contenedor-iconos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.seccion-iconos .contenedor-iconos .contenedor-icono {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  margin: 2rem 0;
}
.seccion-iconos .contenedor-iconos .contenedor-icono .icono-texto h4,
.seccion-iconos .contenedor-iconos .contenedor-icono .icono-texto p {
  text-align: left;
}
.seccion-iconos .contenedor-iconos .contenedor-icono .icono-texto h4 {
  margin: 0 0 1rem 0;
  color: #37371f;
}
.seccion-iconos .contenedor-iconos .contenedor-icono .icono {
  background-color: #ea9010;
  padding: 1rem;
  border-radius: 50%;
}

.products {
  padding-bottom: 4rem;
}
@media (min-width: 480px) {
  .products {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .products {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .products {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.products .product {
  border-radius: 1rem;
  background-color: #FFFFFF;
  margin-bottom: 2rem;
  overflow: hidden;
}
.products .product:last-of-type {
  margin: 0;
}
@media (min-width: 480px) {
  .products .product {
    margin: 0;
  }
}
.products .product .product-content {
  padding: 2rem;
  text-align: center;
}
.products .product .product-content p {
  color: #654b35;
}
.products .product .product-content a {
  margin-top: 2rem;
}
.products .product .product-content .product-price {
  margin-bottom: 3rem;
  color: #ea9010;
  font-weight: 700;
  font-family: "Anton", sans-serif;
  font-size: 2.4rem;
}
.products .product .product-content h3 {
  font-size: 2.2rem;
}

.about-us .about-content {
  padding: 2rem;
  background-color: #FFFFFF;
  border-radius: 1rem;
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .about-us .about-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}
.about-us .about-content .about-image {
  background-image: url("/build/img/about-2.jpeg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 40rem;
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .about-us .about-content .about-image {
    height: unset;
    margin-bottom: unset;
    grid-column: 2/3;
    grid-row: 1/2;
  }
}
.about-us .about-content .about-text p {
  color: #37371f;
}
.about-us .about-content .about-text span {
  color: #ea9010;
  font-size: 2rem;
  font-weight: 700;
}

.explore {
  padding: 4rem 0;
}
.explore p {
  color: #37371f;
}
@media (min-width: 768px) {
  .explore {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}
.explore .explore-content,
.explore .explore-img,
.explore .explore-info,
.explore .explore-seller {
  padding: 2rem;
  background-color: #FFFFFF;
  border-radius: 1rem;
  margin-bottom: 1rem;
}
.explore .explore-content:last-of-type,
.explore .explore-img:last-of-type,
.explore .explore-info:last-of-type,
.explore .explore-seller:last-of-type {
  margin: 0;
}
@media (min-width: 768px) {
  .explore .explore-content,
  .explore .explore-img,
  .explore .explore-info,
  .explore .explore-seller {
    margin: unset;
  }
}
.explore .explore-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  gap: 1rem;
}
@media (min-width: 1024px) {
  .explore .explore-info {
    gap: 3rem;
  }
}
.explore .explore-info p {
  font-size: 2.4rem;
}
.explore .explore-info span {
  color: #90be6d;
  font-weight: 700;
}
.explore .explore-info h2,
.explore .explore-info p {
  margin: 0;
}
.explore .explore-info .price {
  color: #ea9010;
}
.explore .explore-info h2 {
  font-size: 2.8rem;
}
@media (min-width: 768px) {
  .explore .explore-info h2 {
    font-size: 3.6rem;
  }
}
@media (min-width: 768px) {
  .explore .explore-content {
    grid-column: 1/3;
    grid-row: 2/3;
  }
}
.explore .explore-content h4 {
  color: #37371f;
  margin-bottom: 1rem;
}
.explore .explore-content hr {
  color: #c9e3ac;
  opacity: 0.5;
}
.explore .explore-content p {
  margin-top: 2rem;
}
.explore .explore-seller h4 {
  color: #37371f;
  margin-bottom: 1rem;
}
.explore .explore-seller hr {
  color: #c9e3ac;
  opacity: 0.5;
}
.explore .explore-seller p {
  margin-top: 2rem;
}
.explore .explore-seller p span {
  font-weight: 700;
}

.contact {
  padding-bottom: 4rem;
}
@media (min-width: 768px) {
  .contact .contact-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}
.contact .contact-container .contact-content,
.contact .contact-container .contact-form {
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
  margin-bottom: 1rem;
}
.contact .contact-container .contact-content:last-of-type,
.contact .contact-container .contact-form:last-of-type {
  margin: 0;
}
@media (min-width: 768px) {
  .contact .contact-container .contact-content,
  .contact .contact-container .contact-form {
    margin: 0;
  }
}
.contact .contact-container .contact-content h3 {
  color: #ea9010;
}
.contact .contact-container .contact-content p {
  color: #37371f;
}
.contact .contact-container .contact-content span {
  font-style: italic;
}
.contact .contact-container .contact-content .social-title {
  margin: 4rem 0 3rem 0;
  text-align: center;
  color: #37371f;
  font-weight: 700;
}

.form {
  font-family: "PT Sans", sans-serif;
}
.form p {
  color: #37371f;
}
.form label {
  color: #37371f;
  font-weight: 700;
}
.form input:not([type=submit]),
.form textarea,
.form select {
  padding: 1rem;
  border-radius: 0.5rem;
  border: 0.1rem solid #90be6d;
  width: 100%;
  margin: 1rem 0 2rem 0;
}
.form textarea {
  height: 25rem;
}

.social-icons {
  text-align: center;
}
.social-icons svg {
  background-color: #90be6d;
  color: #37371f;
  padding: 1rem;
  border-radius: 100%;
  transition: background-color 300ms ease, transform 300ms ease;
  margin: 1.4rem;
}
@media (min-width: 768px) {
  .social-icons svg {
    margin-top: 0;
    margin-bottom: 0;
  }
}
.social-icons svg:hover {
  background-color: #ea9010;
  transform: scale(1.1);
}

.footer-nav {
  padding: 2rem 0 0 0;
}
@media (min-width: 768px) {
  .footer-nav {
    padding: 3rem 0 0 0;
  }
}
@media (min-width: 768px) {
  .footer-nav .nav {
    text-align: center;
  }
}
@media (min-width: 768px) {
  .footer-nav .nav a {
    padding: 0 3rem;
  }
}

.blog {
  margin-bottom: 4rem;
}

.posts {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .posts {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .posts {
    grid-template-columns: repeat(3, 1fr);
  }
}
.posts .post {
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
  transition: scale 300ms ease;
}
.posts .post:hover {
  scale: 1.05;
}
.posts .post .meta {
  color: #ea9010;
  transition: color 300ms ease;
  cursor: pointer;
}
.posts .post .meta:hover {
  color: #ba730d;
}
.posts .post .post-heading .post-title {
  color: #76ac4c;
  font-size: 2.6rem;
  transition: color 300ms ease;
}
.posts .post .post-heading .post-title:hover {
  color: #90be6d;
}

.entry {
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .entry .entry-head {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.entry .entry-content {
  margin-top: 2rem;
}
.entry .entry-content p {
  color: #37371f;
}
.entry .entry-content hr {
  color: #ea9010;
  opacity: 0.5;
}
.entry .entry-meta p {
  color: #76ac4c;
}

.user-admin {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.admin-buttons {
  padding-top: 2rem;
  display: flex;
  gap: 1rem;
  justify-content: center;
  align-items: center;
}

.table-title {
  margin: 4rem 0;
}

.admin-table {
  display: inline-table;
  width: 100%;
  font-family: "PT Sans", sans-serif;
}
.admin-table:last-of-type {
  margin-bottom: 6rem;
}
.admin-table thead {
  background-color: #37371f;
  color: #FFFFFF;
}
